.selectedPage {
    font-weight: bold;
    color: aqua;
}
.pagination {
    display: flex;
    justify-content: center;
    font-size: 20px;
}
.selectors{
    display: inline-block;
    padding: 8px 16px;
}
.pageNumber {
    cursor: pointer;
}
.pageButtonPrev {
    font-size: 17px;
    background-color: #f1f1f1;
    color: black;
    border-radius: 50%;
    outline: none;
}
.pageButtonPrev:hover {
    background-color: #ddd;
    color: black;
}
.pageButtonNext {
    font-size: 17px;
    background-color: #f1f1f1;
    color: black;
    border-radius: 50%;
    outline: none;
}
.pageButtonNext:hover {
    background-color: #ddd;
    color: black;
}